body {
  margin: 0;
  padding: 0;
  background: url("./img-back.jpg") no-repeat center center fixed;
  background-size: cover;
  font-family: 'Helvetica Neue', Helvetica, Arial, sans-serif;
}

.img-logo {
  width: 60px;
  height: 60px;
  margin-left: 45px;
  margin-top: 20px;
}

.upper {
  background-color: rgb(0 0 0 / 60%);
  padding-bottom: 100px;
}

.bottom {
  background-color: rgb(0 0 0 / 85%);
  color: #757575;
}

.bottom-width {
  max-width: 1000px;
  margin: 0 auto;
  padding: 30px;
}

.bottom-flex {
  display: flex;
  flex-wrap: wrap;
  padding: 15px 0 0;
  margin-block: 0 0;
}

.bottom-flex li {
  list-style: none;
}

.list-bottom {
  width: 25%;
  margin-top: 10px;
}

.link-bottom {
  text-decoration: none;
  color: #757575;
  font-size: 0.8rem;
}

.link-bottom:hover {
  text-decoration: underline;
}

.login {
  margin: 0 auto;
  width: 310px;
  background-color: rgb(0 0 0 / 60%);
  border-radius: 5px;
  padding: 30px 70px 143px;
}

.login h1 {
  color: white;
  padding-bottom: 10px;
}

.input-text {
  margin-bottom: 20px;
}

.input-text input {
  width: 100%;
  height: 45px;
  background-color: #333;
  color: white;
  border-radius: 5px;
  border: none;
  outline: transparent;
  text-indent: 18px;
}

.input-text input::input-placeholder {
  font-size: 1rem;
  color: #999;
}

.input-text input::placeholder {
  font-size: 1rem;
  color: #999;
  text-indent: 40px;
}

.signin-button {
  margin-top: 20px;
  width: 100%;
  padding: 13px;
  border-radius: 5px;
  background-color: red;
  color: white;
  border: none;
  font-weight: bold;
  font-size: 1.05rem;
  cursor: pointer;
}

.remember-flex {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  font-size: 0.8rem;
}

.color_text {
  color: #b3b3b3;
}

.color_link {
  color: #737373;
}

.signup-link {
  color: white;
  text-decoration: none;
}

.warning-input {
  display: none;
  color: #e87c03;
  font-size: 0.8rem;
  margin-top: 6px;
}

.signup-link:hover {
  text-decoration: underline;
}

.face_icon {
  color: #3b5998;
  margin-right: 6px;
  font-size: 20px;
}

.log_face {
  text-decoration: none;
  margin-left: 10px;
  font-size: 0.8rem;
}

.login-face {
  margin: 50px 0 15px;
  vertical-align: middle;
  color: #8c8c8c;
}

.new-members {
  margin-bottom: 10px;
  color: #8c8c8c;
}

.protection {
  font-size: 0.8rem;
  color: #8c8c8c;
}

.protection a {
  text-decoration: none;
}

.protection a:hover {
  text-decoration: underline;
}

.help a {
  text-decoration: none;
}

.help a:hover {
  text-decoration: underline;
}

.tel-link {
  text-decoration: none;
  color: #757575;
}

.tel-link:hover {
  text-decoration: underline;
}

.select-language {
  appearance: none;
  background: url("data:image/svg+xml;base64,");
  background-size: 12px;
  background-position-x: calc(100% - 7px);
  background-position-y: calc(100% - 14px);
  background-repeat: no-repeat;
  background-color: #000;
  color: #757575;
  padding: 14px 20px 14px 14px;
  margin: 30px 0 40px;
  font-size: 0.9rem;
  min-height: 40px;
  border: 1px solid #333;
}

.select-language option {
  padding-left: 10px;
}

@media screen and (width <= 740px) {
  body {
    background: black;
  }

  .img-logo {
    margin-left: 22px;
  }

  .login {
    padding: 0 10px;
    width: 100%;
  }

  .login-div {
    width: 90%;
    margin: 0 auto;
  }

  .bottom {
    border-top: 1px solid #737373;
  }

  .upper {
    padding-bottom: 93px;
  }

  .list-bottom {
    width: 33%;
  }
}

@media screen and (width <= 500px) {
  .list-bottom {
    width: 50%;
  }
}
